'use client'

import { useEffect, useState } from 'react'
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
} from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select'

type CropHistory = {
  id: number
  field: number
  farmer_name: string
  crop_name: string
  crop_rotation_pattern: string
  cultivated_area_last_year: number
  planned_area_this_year: number
  variety_cultivated: string
  average_yield: number
}

export function CropsList() {
  const [allHistory, setAllHistory] = useState<CropHistory[]>([])
  const [filteredHistory, setFilteredHistory] = useState<CropHistory[]>([])
  const [selectedField, setSelectedField] = useState<string>('all')
  const [loading, setLoading] = useState(false)

  // Fetch crop history
  useEffect(() => {
    const fetchHistory = async () => {
      setLoading(true)

      const token = typeof window !== 'undefined'
        ? localStorage.getItem('authToken')
        : null

      if (!token) return

      const res = await fetch(
        'https://rda.ngrok.app/api/crop-history/',
        {
          headers: {
            Authorization: `Bearer ${token}`,
            'ngrok-skip-browser-warning': 'true',
          },
        }
      )

      const data = await res.json()

      const history = Array.isArray(data)
        ? data
        : data.results || data.data || []

      setAllHistory(history)
      setFilteredHistory(history)
      setLoading(false)
    }

    fetchHistory()
  }, [])

  // Unique fields
  const uniqueFields = Array.from(
    new Map(
      allHistory.map((item) => [
        item.field.toString(),
        { id: item.field.toString(), name: item.farmer_name },
      ])
    ).values()
  )

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle>Select Field</CardTitle>
        </CardHeader>
        <CardContent>
          <Select
            value={selectedField}
            onValueChange={(value) => {
              setSelectedField(value)
              value === 'all'
                ? setFilteredHistory(allHistory)
                : setFilteredHistory(
                    allHistory.filter(
                      (item) => item.field.toString() === value
                    )
                  )
            }}
          >
            <SelectTrigger>
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Farmers</SelectItem>
              {uniqueFields.map((field) => (
                <SelectItem key={field.id} value={field.id}>
                   {field.name}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </CardContent>
      </Card>

      {loading && <p className="text-muted-foreground">Loading crop history…</p>}

      {!loading &&
        filteredHistory.map((item) => (
          <Card key={item.id} className="hover:shadow-lg">
            <CardContent className="pt-6">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="space-y-3">
                  <div className="flex items-center justify-between">
                    <h3 className="text-xl font-bold capitalize">
                      {item.crop_name}
                    </h3>
                    <Badge variant="outline">
                      {item.crop_rotation_pattern}
                    </Badge>
                  </div>

                  <p className="text-sm text-muted-foreground">
                    Field: {item.farmer_name}
                  </p>

                  <div className="grid grid-cols-2 gap-3">
                    <div className="bg-muted/50 rounded p-2">
                      <p className="text-xs text-muted-foreground">Variety</p>
                      <p className="font-semibold text-sm">
                        {item.variety_cultivated}
                      </p>
                    </div>
                    <div className="bg-muted/50 rounded p-2">
                      <p className="text-xs text-muted-foreground">Avg Yield</p>
                      <p className="font-semibold text-sm">
                        {item.average_yield}
                      </p>
                    </div>
                  </div>
                </div>

                <div className="space-y-3">
                  <div className="grid grid-cols-2 gap-3">
                    <div className="bg-primary/10 rounded p-2">
                      <p className="text-xs text-muted-foreground">
                        Last Year Area
                      </p>
                      <p className="font-semibold text-sm text-primary">
                        {item.cultivated_area_last_year} acres
                      </p>
                    </div>
                    <div className="bg-secondary/10 rounded p-2">
                      <p className="text-xs text-muted-foreground">
                        Planned Area
                      </p>
                      <p className="font-semibold text-sm text-secondary">
                        {item.planned_area_this_year} acres
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
        ))}

      {!loading && filteredHistory.length === 0 && (
        <p className="text-muted-foreground">No crop history found.</p>
      )}
    </div>
  )
}
